.app {
  display: flex;
  margin: 0 auto;
  width: 1510px;
  height: 1024px;
  background-color: #e4ebfa;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}

.background-color{
  width: 1510px;
  height: 1024px;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  background-color: black;
  opacity: 0.5;
  z-index: 1;
}




